<template lang="pug">
    v-data-table(:headers="headers"
        :items="tabledata"
        :options.sync="options"
        :expanded.sync="expanded"
        fixed-header
        show-expand
        :height="height"
        item-key="field"
        no-data-text="暂无数据"
        :server-items-length="total_rows"
        class="elevation-4")

        template(v-slot:top)
            v-toolbar(flat)
                v-toolbar-title 数据表
                v-divider(class="mx-4" insert vertical)
                v-spacer

                v-dialog(v-model="dialog" max-width="500px")
                    template(v-slot:activator="{ on, attrs }")
                        v-btn(color="primary" 
                                v-bind="attrs"
                                v-on="on" )
                            v-icon(style="height:auto") playlist_add

                    v-card
                        v-card-title 
                            span(class="headline") {{ formTitle }}
                        v-card-text
                            v-container
                                v-row 
                                    v-col(cols="12" sm="6" md="4")
                                        v-text-field(v-model="editedItem.field" label="字段")
                                    v-col(cols="12" sm="6" md="4")
                                        v-text-field(v-model="editedItem.dtype" label="类型")
                                    v-col(cols="12" sm="6" md="4")
                                        v-text-field(v-model="editedItem.showfield" label="描述")
                                v-row 
                                    v-col(cols="12" sm="6" md="4")
                                        v-switch(v-model="editedItem.ismust" label="是否必须")
                                    v-col(cols="12" sm="6" md="4")
                                        v-switch(v-model="editedItem.isunique" label="是否主键")
                                    v-col(cols="12" sm="6" md="4")
                                        v-switch(v-model="editedItem.isindex" label="是否索引")
                                v-row
                                    v-col(cols="12" sm="6" md="4")
                                        v-switch(v-model="editedItem.isdate" label="是否日期")
                                    v-col(cols="12" sm="6" md="4")
                                        v-text-field(v-model="editedItem.index" label="优先级")

                        v-card-actions
                            v-spacer
                            v-btn(color="blue darken-1" text @click="close") 取消
                            v-btn(color="blue darken-1" text @click="save") 确定

                v-divider(class="mx-4" insert vertical)

                v-btn(color="primary" fab   small @click="upload_config")
                    v-icon(style="height:auto") cloud_upload
                
                
        
        template(v-slot:expanded-item="{ headers, item }")
            td(:colspan="headers.length")
                pre {{item}}

        template(v-slot:item.actions="{item}")
            v-icon(small class="mr-2" @click="editItem(item)") mdi-pencil
            v-icon(small class="mr-2" @click="deleteItem(item)") mdi-delete

            

            
</template>

<script>
// 扩展表格显示
// 2020/5/25 by oldkingnearby@gmail.com
import { sync} from 'vuex-pathify'
import * as api from "@/apis/wswebapi"
export default {
    data:()=>({
        dialog: false,
        editedItem:{
            field:"",
            dtype:"",
            ismust:false,
            showfield:"",
            isunique:false,
            isdate:false,
            isindex:false,
            index:100
        },
        defaultItem:{
            field:"",
            dtype:"",
            ismust:false,
            showfield:"",
            isunique:false,
            isdate:false,
            isindex:false,
            index:100
        },
        height:400,
        expanded: [],
        singleExpand:true,//只允许单行扩展
        // 分页数据管理
        options:{},
        items_per_page_options:[50, 100, 500, 1000],
        // 显示的数据库配置
        // collection_config:sync('db/collection_config'),
        // 表头
        headers:[{text:"字段",value:"field"},{text:"类型",value:"dtype"},{text:"是否必须",value:"ismust"},{text:"描述",value:"showfield"},
        {text:"是否主键",value:"isunique"},{text:"是否索引",value:"isindex"},{text:"是否日期",value:"isdate"},{text:"优先级",value:"index"},{text:"操作",value:"actions"}],
        // 表内数据
        tabledata:[],
        // 总行数
        total_rows:100,
        editedIndex:-1,
    }),
    computed: {
        collection:sync('db/collection'),
        formTitle () {
            return this.editedIndex === -1 ? '新建字段' : '编辑字段'
        },
    },

    methods:{
        // 刷新表格内容
        update_col_page_data(){
            // this.$store.dispatch("db/update_col_page_data",this.options);
        },
        // 编辑
        editItem(item){
            this.editedIndex = this.tabledata.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        // 删除
        deleteItem(item){
            const index = this.tabledata.indexOf(item)
            confirm('确定删除此字段吗?') && this.tabledata.splice(index, 1)
        },
        close(){
            this.dialog = false;
            this.editedIndex = -1;
            this.editedItem=this.defaultItem;
        },
        save(){
            if (this.editedIndex > -1) {
                Object.assign(this.tabledata[this.editedIndex], this.editedItem)
            } else {
                this.tabledata.push(this.editedItem)
            }
            this.close();
        },
        upload_config(){
            for (var i =0;i<this.tabledata.length;i++){
                this.tabledata[i].index=Number(this.tabledata[i].index);
            }
            confirm('确定替换数据库字段吗?') && api.UpdateCollectionFieldInfo(this.collection, this.tabledata).then(res=>{
                if (res.code==200){
                    this.$store.dispatch('msglog/push_msg',{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch('msglog/push_msg',{message:res.msg+res.error,type:"error"})
                }
            });
        }
    },
    watch:{
        options:{
            handler(){
              
            },
            deep:true,
        },
        collection(){
            if (this.collection){
                api.GetCollectionFieldInfo(this.collection).then(res=>{
                    if (res.code==200){
                        this.tabledata=res.data;
                        this.total_rows=res.data.length;
                    }
                })
            }
            
        }
    }

}
</script>